import './Home.scss';
import Button from '../button/Button';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function Home() {

    window.scrollTo(0, 0)

    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 2
                }
            },
            {
                breakpoint: 700,
                settings: {
                    slidesToShow: 1
                }
            }
        ]
    };

    return (
        <div className="home">
            <header>
                <div className="container">
                    <h2>
                    Управляй информацией, управляй миром!
                </h2>
                    <p>
                        GrossBee – компания, объединившая профессионалов ИТ – отрасли в трех городах Украины.
                </p>
                    {/* <Button text='About us' /> */}
                </div>
            </header>

            <main>
                <section className='about'>
                    <div className="container">
                        <h2>
                            О нас
                        </h2>
                        <p>
                            Наша команда состоит из профессиональных IT-специалистов. Мы предлагаем готовые решения для поиска новых клиентов. А именно, функциональный сайт, который расширяет границы вашего бизнеса и гарантировано увеличивает прибыль.
                        </p>
                        <p>
                            Мы используем комплексный подход! У вас нет времени разбираться во множестве тонкостей по выбору типа сайта и необходимой рекламе? Мы возьмем на себя все эти сложности. Вам необходимо всего лишь описать Ваш проект и ответить на несколько вопросов менеджера проекта, который самостоятельно подготовит ТЗ и планирование проекта.
                        </p>
                    </div>

                    <div className="slider">
                        <Slider {...settings}>
                            {
                                [['Одностраничник', 'Закажите Landing (сайт «одностраничник»), если вам нужна реклама бренда или товара в короткий срок. Landing Page создается исключительно для рекламной кампании.'], ['САЙТ-ВИЗИТКА', 'Грамотно разработанный сайт-визитка является не только визитной карточкой, но и эффективной рекламой, круглосуточным офисом, и даже лицом компании в интернете.'], ['КОРПОРАТИВНЫЙ САЙТ', 'Корпоративный сайт представляет лицо Вашей компании. Сегодня никому не важно какой у вас офис или как выглядят ваши сотрудники, первое впечатление от компании получают, смотря на ее сайт.'], ['ИНТЕРНЕТ МАГАЗИН', 'Хотите, чтобы ваши интернет-продажи были эффективными, приносили доход, доверьте создание интернет-магазина профессионалам!'], ['УНИКАЛЬНЫЙ САЙТ', 'У вас есть огромное желание выделиться среди сотен ваших конкурентов, выразить вашу индивидуальность — закажите уникальный сайт.'], ['АУДИТ САЙТА И РЕКЛАМЫ', 'Дает возможность добиться быстрого продвижения сайта в поисковых системах, устранив технические неисправности сайта. Аудит позволяет выявить недостатки сайта, по поисковой оптимизации, непривлекательному и неудобному для пользователей дизайну, наличие ошибок кода и многое другое.']].map(card =>
                                    <div className="card">
                                        <h6>
                                            {card[0]}
                                        </h6>
                                        <p>
                                            {card[1]}
                                        </p>
                                    </div>
                                )
                            }
                        </Slider>
                    </div>
                </section>

                <section className='contactUs'>
                    <div className="container">
                        <h2>
                            Присоединяйся к нам уже сегодня
                        </h2>
                        <Link to='/Project_2____GROSS_BEE/contact-us'>
                            <Button text='Contact us now' />
                        </Link>
                    </div>
                </section>
            </main>
        </div >
    )
}