let name, tel, email, subject, text, form;

function showToast(type, text) {
    const toast = document.querySelector('.toast');
    toast.classList.add(type);
    toast.textContent = text;
    setTimeout(() =>
        toast.classList.remove(type), 3000
    )
}

export default async function SendMessage(event, validator) {
    event.preventDefault();

    const adress = `https://grossbee.agency/api/sendMessage.php`;
    form = event.target;

    name = form.name;
    email = form.email;
    tel = form.tel;
    subject = document.querySelector('.select > li');    

    if (name.value.trim().length < 2) {
        name.classList.add('error');
        showToast('error', 'Минимальная длина: 2 символа');
        return;
    } else name.classList.remove('error');

    if (!validator.isMobilePhone(tel.value, 'any')) {
        tel.classList.add('error');
        showToast('error', `Некорректный номер телефона`)
        return;
    } else tel.classList.remove('error');

    if (subject.innerText === 'По умолчанию') {
        subject.classList.add('error');
        showToast('error', `Выберите тему обращения`);
        return
    } else subject.classList.remove('error');


    text = encodeURI(
        `Имя: ${name.value}
Почта: ${email.value}
Номер телефона: ${tel.value}
Тема: ${subject.innerText}`);

    return fetch(adress, {
        method: "POST",
        body: text,
    })
        .then((data) => {
            if (data.ok)
                showToast('success', 'Сообщение успешно доставлено')
            else
                showToast('error', 'Произошла ошибка, попробуйте позже')
        });
}