import './Select.scss';
import { useState } from 'react';

export default function Select() {

    const [isOpen, setIsOpen] = useState();
    const [value, setValue] = useState('По умолчанию')

    return (
        <ul className='select' >
            <li onClick={() => setIsOpen(!isOpen)}>
                {value}
            </li>

            <ul className={`list ${isOpen ? 'open' : ''}`}>
                {
                    ['По умолчанию', 'Заказать услугу', 'Отклик на вакансию'].map(li =>
                        <li onClick={() => {setValue(li); setIsOpen(false)}}>
                            {li}
                        </li>
                    )
                }
            </ul>
        </ul>
    )
}